/* 
** Tout ce qui concerne de la normalisation supplémentaire
*/


/*normalisation avec pseudo éléments */
*, ::before, ::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, li, ul, ol, a {
  margin: 0;
  padding: 0;
}

ul,
ol, 
li {
  list-style-type: none;
}